header {
    display: flex;
    justify-content:space-between;
    align-items: center;
    width: 100%;
    background-color: var(--store-color);
    position:sticky;
    top: 0;
    z-index: 10;
}

.store-logo {
    font-family: 'always__foreverregular';
    font-size: 4rem;
    margin-left: 3rem;
    margin-top: .5rem;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.store-logo:hover {
    color: goldenrod;
    cursor: pointer;
}

a, a:visited, a:active {
    color: inherit;
    text-decoration: none;
}


header a:hover {
    color: goldenrod;
    fill: goldenrod;
}

header > ul {
    list-style: none;
    display: flex;
    gap: 5rem;
    margin-right: 7rem;
    color: white;
    font-size: 2rem;
    margin-left: auto;
}

 .home {
    display: flex;
    justify-content: center;
    align-items: center;
    fill: white;
 }

 .shop, .cart-button {
    display: flex;
    justify-content: center;
    align-items:baseline;
    fill: white;
 }

 header ul li:hover {
    color: goldenrod;
    fill: goldenrod;
    cursor: pointer;
 }

.home-icon {
    height: 30px;
    width: 30px;
    margin-left: 10px;
    fill: inherit;
}

.shop-icon {
    height: 25px;
    width: 25px;
    margin-left: 10px;
    fill: inherit;
}

.cart-icon {
    height: 23px;
    width: 23px;
    margin-left: 12px;
    fill: inherit;
    position: relative;
}

.quantity-icon {
    font-size: 1rem;
    width: 1.5rem;
    height: 1.5rem;
    padding: .3rem;
    border-radius: 100%;
    background-color: maroon;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(5%, -70%);

}